:root {
  --font-body: Arial, sans-serif;
  --font-inter: "Inter", sans-serif;
  --font-lexend: "Lexend Deca", sans-serif;
}

:root {
  --black: hsl(0, 0%, 0%);
  --verydarkblue: hsl(233, 47%, 7%);
  --darksaturatedblue: hsl(244, 38%, 16%);
  --softviolet: hsl(277, 64%, 61%);
  --white: hsl(0, 0%, 100%);
  --whitemain: hsla(0, 0%, 100%, 0.75);
  --whitestat: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 15px;
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.6rem;
  max-width: 74rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}